html, body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow:hidden;
  overflow: -moz-hidden-unscrollable;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	text-align: center;
}

.App-header {
	/* background-color: #282c34; */
	background-image: -webkit-radial-gradient(#20293b, #071024);
	background-image: radial-gradient(#20293b, #071024);
	min-height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	font-size: calc(10px + 1vmin);
	color: white;
}

.stage {
	/* -webkit-transition: all 0.2s ease-out;
	-moz-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out; */
}

.radial-controller {
	position: absolute;
	margin: 10px;
	bottom: 0vh;
	left: 0vw;
}

.settings-controller {
	position: absolute;
	margin: 10px;
	bottom: 0vh;
	right: 0vw;
	width: 40%;
	max-width: 500px;
}

.settings-form {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	height: auto;
	background-color: rgba(0, 0, 0, 0.5);
	border: 1px solid rgba(0, 0, 0, 0.5);
}

.settings-form * {
	margin: 5px;
}

.settings-controllers {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	width: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: baseline;
	        align-items: baseline;
}

.controller-label {
	background: none;
	width: 35%;
	border: none;
	font-size: 1.2rem;
	font: bold;
	color: white;
}

.text-fields {
	background: none;
	width: 10%;
	border: none;
	font-size: 1.2rem;
	font: bold;
	color: white;
}

.sliders {
	-webkit-appearance: none;
	width: 55%;
	height: 5px;
	border-radius: 5px;
	background: white;
	outline: none;
	opacity: 0.7;
	-webkit-transition: 0.2s;
	-webkit-transition: opacity 0.2s;
	transition: opacity 0.2s;
}

.slidersHalf {
	-webkit-appearance: none;
	width: 25%;
	height: 5px;
	border-radius: 5px;
	background: white;
	outline: none;
	opacity: 0.7;
	-webkit-transition: 0.2s;
	-webkit-transition: opacity 0.2s;
	transition: opacity 0.2s;
}

.sliders::-webkit-slider-thumb,
.slidersHalf::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 15px;
	height: 15px;
	border-radius: 50%;
	background: white;
	cursor: pointer;
}

.sliders::-moz-range-thumb,
.slidersHalf::-webkit-slider-thumb {
	width: 15px;
	height: 15px;
	border-radius: 50%;
	background: white;
	cursor: pointer;
}

.handlebars {
	background-image: -webkit-linear-gradient(left, rgba(256, 256, 256, 0), rgba(256, 256, 256, 0.15), rgba(256, 256, 256, 0.3));
	background-image: linear-gradient(90deg, rgba(256, 256, 256, 0), rgba(256, 256, 256, 0.15), rgba(256, 256, 256, 0.3));
	color: rgba(256, 256, 256, 0.15);
	-webkit-transition: color 0.5s ease-out;
	transition: color 0.5s ease-out;
}

.handlebarshover {
	background-image: -webkit-linear-gradient(left, rgba(256, 256, 256, 0), rgba(256, 256, 256, 0.3), rgba(256, 256, 256, 0.5));
	background-image: linear-gradient(90deg, rgba(256, 256, 256, 0), rgba(256, 256, 256, 0.3), rgba(256, 256, 256, 0.5));
	color: rgba(256, 256, 256, 1);
}

